import React from 'react';
import EntityContainer from 'modules/entity/containers';
import { Pagination, TableBody, TableContainer, TableHead } from 'shared/components/Elements';
import {
	ActionsButtons,
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';
import { useAppState } from 'shared/state';
import { usePagination, useRouter } from 'shared/hooks';
import { useDebounce } from 'use-debounce';
import { useCSVList } from '../../hooks';
import { CSVType } from '../../types';
import { useCSVManagementContext } from '../../contexts';

export function CSVList() {
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query } = useCSVManagementContext();
	const { match, history } = useRouter();
	const [searchQuery] = useDebounce<string>(query, 500);

	const { deleteLoading, deleteCSVHandler, showDeleteVisible, closeDeletePopup, deleteVisible } =
		useCSVList();

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="csv-list"
					name="AllCsvList"
					url={`/${companyId}/my/company/imports/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const csvList = items as CSVType[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '20%' }}>
													File Name
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '10%' }}>
													Created By
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '7%' }}>
													Created Date
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '11%' }}>
													<div className="flex-center-end">Actions</div>
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{csvList.map((item) => {
												const { id, created_by, created_date, file_name } = item;
												return (
													<StyledTableRow key={id}>
														<StyledTableCell>
															<Link to={`${match.path}/view/${item.id}`}>
																<span className="text-primary transition hover:text-primary-dark">
																	{file_name}
																</span>
															</Link>
														</StyledTableCell>
														<StyledTableCell>{created_by.name}</StyledTableCell>
														<StyledTableCell>
															{dayjs(created_date).format('MMMM Do YYYY, h:mm a')}
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-end ">
																<ActionsButtons.DeleteButton
																	clickHandler={() => showDeleteVisible(item)}
																/>
																<ActionsButtons.ViewButton
																	clickHandler={() => history.push(`${match.path}/view/${item.id}`)}
																	// isDisabled={!item.owner}
																/>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteCSVHandler()}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={`Are you sure to delete  ${deleteVisible?.file_name}?`}
									hasLoader
									isLoading={deleteLoading}
								/>
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}
